.maskDiv {
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.limit {
  font-size: 12px;
  margin-top: -3px;
  margin-left: 16px;
  color: #333; }

.limit ~ div textarea + div {
  top: 170px !important; }

.textArea {
  margin-right: 0;
  margin-left: 0; }

.noBorder {
  position: static;
  overflow: auto;
  margin-top: 0; }

.noBorder > div {
  position: static; }

.noBorder textarea + div {
  position: absolute;
  z-index: 100;
  top: 150px;
  right: 15px; }

.noBorder::before,
.noBorder::after,
.imagesUploaderContainer > div > div::before {
  display: none; }

.imagesUploaderContainer > div > div:first-child + div {
  margin-top: 0; }

.imagesUploader div:first-child {
  padding-bottom: 0; }

.imagesUploader ul li,
.imagesUploader ul + div {
  width: 40px;
  height: 40px; }

.content {
  font-size: 14px;
  margin-right: 12px;
  margin-left: 12px;
  color: #333; }

.imgList {
  overflow: hidden;
  margin-right: 12px;
  margin-left: 12px; }

.imgList button {
  float: left;
  overflow: hidden;
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  background-color: transparent; }

.imgList img {
  float: left;
  width: 40px;
  height: 40px;
  margin-right: 4px;
  margin-bottom: 4px; }



