.container {
  width: 128px;
  height: 160px;
  border-radius: 5px;
  background-color: #FFF;
  -webkit-box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.05);
          box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.05);
  padding-top: 16px;
  position: relative; }

.topLayerButton {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 100;
  cursor: pointer; }

.avatar {
  margin-left: auto;
  margin-right: auto;
  width: 64px;
  height: 64px;
  border-radius: 32px;
  background: transparent center no-repeat;
  background-size: cover;
  position: relative; }

.statusContainer {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 16px;
  height: 16px;
  border-radius: 8px;
  overflow: hidden; }

.attendanceContainer {
  background-color: #4DB553; }

.lateContainer {
  background-color: #FF5B05; }

.skipClassesContainer {
  background-color: #333; }

.status {
  width: 16px;
  height: 16px;
  display: inline-block;
  -webkit-mask-size: 16px 16px;
          mask-size: 16px 16px;
  background-color: #FFF; }

.attendance {
  -webkit-mask-image: url("images/attendance.svg");
          mask-image: url("images/attendance.svg"); }

.late {
  -webkit-mask-image: url("images/late.svg");
          mask-image: url("images/late.svg"); }

.skipClasses {
  -webkit-mask-image: url("images/skipClasses.svg");
          mask-image: url("images/skipClasses.svg"); }

.name {
  text-align: center;
  font-size: 14px;
  color: #233438;
  margin-top: 16px; }

.studentId {
  text-align: center;
  font-size: 12px;
  color: #666;
  margin-top: 8px; }



