.container {
  background-color: #F2F2F2;
  padding-top: 16px; }

.title {
  height: 20px;
  overflow: hidden;
  font-size: 0;
  padding-bottom: 24px; }

.titleText {
  float: left; }
  .titleText i {
    margin-left: 24px;
    margin-right: 8px;
    display: inline-block;
    width: 16px;
    height: 16px;
    border-radius: 3px;
    margin-bottom: -2px; }
  .titleText span {
    font-size: 14px;
    color: #333; }

.foldButton {
  float: right;
  display: inline-block;
  width: 16px;
  height: 16px;
  -webkit-mask-size: 16px 16px;
          mask-size: 16px 16px;
  -webkit-mask-image: url("images/expandLess.svg");
          mask-image: url("images/expandLess.svg");
  -webkit-transition: -webkit-transform 0.5s ease;
  transition: -webkit-transform 0.5s ease;
  -o-transition: transform 0.5s ease;
  transition: transform 0.5s ease;
