  margin-right: 24px; }
  .foldButton:active {
    background-color: #AAA; }
  .foldButton button {
    display: block;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer; }

.foldButtonClose {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg); }

.content {
  overflow: hidden;
  -webkit-transition: height 0.5s ease;
  -o-transition: height 0.5s ease;
  transition: height 0.5s ease; }
  .content::after {
    content: '';
    width: 100%;
    height: 0;
    display: block; }

.floatLeft {
  float: left; }

.contentItem {
  margin-left: 24px;
  margin-bottom: 24px; }



