.answerContent {
  display: block;
  height: 40px;
  position: relative;
  overflow: hidden;
  outline: none;
  margin-left: 12px;
  margin-right: 12px;
  border-bottom: solid 2px #C8C8C8; }

.circleOrder {
  width: 20px;
  height: 20px;
  background-color: #FDBE41;
  border-radius: 10px;
  position: absolute;
  top: 10px;
  left: 2px;
  font-size: 12px;
  color: #383F4A;
  text-align: center;
  line-height: 20px; }



